import { Controller } from '@ali/4ever-cangjie';
import { BlockLinkCard, ILinkCardInfo, LinkPluginConfig } from '../../types';
import { fetchLinkData } from '../utils/fetchLinkData';


export default function updateLinkCard(
  controller: Controller,
  node: BlockLinkCard,
  data: {
    text: string,
    href: string,
    cardInfo?: ILinkCardInfo,
    getLinkInfo?: LinkPluginConfig['getLinkInfo']
  }
) {

  const { text, href, cardInfo, getLinkInfo } = data;

  // 将 text link 转换为 block link card
  const nodeText = text.length ? text : href;

  // 如果已经有 cardInfo，则不需要拉取链接信息
  const needFetchLinkInfo = !Boolean(cardInfo);

  // 如果更新了卡片信息
  if (needFetchLinkInfo) {
    fetchLinkData(node, controller, href, nodeText, getLinkInfo);
  }
  return controller;
};

